import React, { Component } from 'react';
import { PrimaryButton, Stack, StackItem } from '@fluentui/react';
function FluentUiDemo() {
    return (
        <div className="row  m-4" >
            <Stack horizontal gap="10" wrap >
                <StackItem>
                    <PrimaryButton onClick={() => { alert("Im Clicked") }}>Primary</PrimaryButton>
                </StackItem>
                <StackItem>
                    <PrimaryButton primary={false} onClick={() => { alert("Im Clicked") }}>Secondary</PrimaryButton>
                </StackItem>
                <StackItem>
                    <PrimaryButton primary={false} onClick={() => { alert("Im Clicked") }}>Secondary</PrimaryButton>
                </StackItem>
                <StackItem>
                    <PrimaryButton primary={false} onClick={() => { alert("Im Clicked") }}>Secondary</PrimaryButton>
                </StackItem>
                <StackItem>
                    <PrimaryButton primary={false} onClick={() => { alert("Im Clicked") }}>Secondary</PrimaryButton>
                </StackItem>
                <StackItem>
                    <PrimaryButton primary={false} onClick={() => { alert("Im Clicked") }}>Secondary</PrimaryButton>
                </StackItem>
                <StackItem>
                    <PrimaryButton primary={false} onClick={() => { alert("Im Clicked") }}>Secondary</PrimaryButton>
                </StackItem>
                <StackItem>
                    <PrimaryButton primary="false" onClick={() => { alert("Im Clicked") }}>Secondary</PrimaryButton>
                </StackItem>
            </Stack>
        </div>)
}

export default FluentUiDemo;